import React, { PureComponent, Suspense } from "react";
import { managePostHeight } from "../actions/themeActions";
import { connect } from "react-redux";
import { rID } from "../utils";
import { CloudHostUrl, CloudPath, uploadedUrl } from "../constants";
import { suiteTrack } from "../actions/api";
import { updateDataTheme } from "../actions/displayThemeAction";
import UpgradeScreenPopup from "./Components/PostPopUp/Elements/UpgradeScreenPopup";
import  ThemeRoute  from "./Themes";

const Header = React.lazy(() => import("./Header"));
const Filters = React.lazy(() => import("./Components/Filters"));
const OnsiteUploadButton = React.lazy(() =>  import("./Components/UserComponents/OnsiteUploadButton"));
const TikTok_Img_update = React.lazy(() => import("./Components/customFunction/tikTok_Img_update"));
const Announcements = React.lazy(() => import("./Components/announcementOverlay"));
const TopTicker = React.lazy(() => import("./Components/Tickers/topTicker"));
const BottomTicker = React.lazy(() => import("./Components/Tickers/bottomTicker"));
class App extends PureComponent {
  constructor(props) {
    super(props);
    this.state = {
      containerSize: {
        width: null,
        height: null,
        updateData : 0,
        see: false
      },
      parentID: "",
    };
  }


  componentDidMount() {
    window.addEventListener("storage", this.readAuthenticationCode);
    const containerEle = document.querySelector(".tb_app_container");
    
    this.setState({
      containerSize: {
        width: containerEle ? containerEle.offsetWidth : null,
        height: containerEle ? containerEle.offsetHeight : null,
      },
    });
  }

  readAuthenticationCode = (event) => {
    if (event.key == "socketData" && event.newValue) {
      localStorage.removeItem("socketData");
      this.updateThemedata(JSON.parse(event.newValue));
    }
  };


    updateThemedata = (event) => {
      if (event && Object.keys(event).length > 0) {
        if (event.type == "prepend") {
          this.setState({updateData : Math.random()},() => this.props.updateDataTheme(event))
        } else if (event.type == "remove") {
          this.setState({updateData : Math.random()},() => this.props.updateDataTheme(event))
        }
      }
    };

  componentDidUpdate() {
    let p_class = document.querySelector(".tb_app_container");
    if(p_class){
      let P_id = rID ? rID : p_class.parentNode.id;
      this.setState({ parentID: P_id });
    }
  }

  componentWillUnmount() {
    window.removeEventListener("storage", this.readAuthenticationCode);
  }

  render() {
    const { wall, webFilters, postData, languageSetting, filter_tags, announcements } = this.props;
 
    const { isDisplay, isEditor, ThemeRule, UgcSettings, Banner, CustomTicker, Background, CustomBannerImage, LogoImage, BannerImage } = wall
    const { see } = this.state;

    const headerPositionTop = Banner && Object.keys(Banner).length > 0 && ((Banner.status == 1 && Banner.banner_position == 1) || BannerImage.status == 1) ? true : false;
        
    const headerPositionBottom = Banner && Object.keys(Banner).length > 0 && Banner.status == 1 && Banner.banner_position == 2 ? true : false;
     
    const filters = ![55, 52, 60, 16, 47].includes(ThemeRule.themeId) ? true : false;
    const web_Filter_Status = webFilters && webFilters.length > 1 ? true : false;
    const teg_Filter_Status = filter_tags && filter_tags.length > 0 ? true : false;

    let brandNet =  webFilters != "" && webFilters ? webFilters.map(({ networkId }) => networkId) : null;

    if (Background.image && Background.image.includes(CloudHostUrl)){
      if(Background.image.includes(`${CloudHostUrl}uploaded/`)){
        Background['image'] = String(Background.image).replace(`${CloudHostUrl}uploaded/`, "");
      }else if(Background.image.includes(`${CloudPath}uploaded/`)){
        Background["image"] = String(Background.image).replace(uploadedUrl, "");
      }
    }

    const backgroundStyle = !Background.transparent ? { 
      backgroundImage: Background.image ? `url(${Background.image})` : "",
      backgroundColor: Background.color 
    } : { backgroundColor: 'transparent'};

    return (
     <div className="tb_app_container temp-class">
        {headerPositionTop ? (
          <Suspense fallback={<></>}>
            <Header wall={wall} isDisplay={isDisplay} Banner={Banner} BannerImage={BannerImage} LogoImage={LogoImage} CustomBannerImage={CustomBannerImage} />
          </Suspense>
        ) : null}

      {isDisplay && CustomTicker && Object.keys(CustomTicker).length > 0 && CustomTicker.status === 1 &&
          CustomTicker.ticker_position === "1" ? (<TopTicker
            CustomTicker={CustomTicker}
            wall={wall}
            UgcSettings={UgcSettings}
          />
         ) : null}

        <div className="tb_app_wrapper" style={backgroundStyle}>
          {(!isDisplay && wall.UserRule.on_site_upload && UgcSettings.onsite_status && UgcSettings.onsite_button_status && ![74, 75, 76, 77].includes(ThemeRule.themeId)) || (suiteTrack && wall.UgcSettings.onsite_status) ? (
            <Suspense fallback={<></>}>
              <OnsiteUploadButton UgcSettings={UgcSettings} id={wall.Wall.id} />
            </Suspense>
          ) : null}
          

          {/* {!isDisplay && filters && (web_Filter_Status || teg_Filter_Status) &&
          ![70, 71, 73, 74, 75, 76, 77].includes(ThemeRule.themeId) ? (
            <Suspense fallback={<></>}>
              <Filters wall={wall} languageSetting={languageSetting} postData={postData} webFilters={webFilters} filter_tags={filter_tags}  appendData={postData.appendData}  type={wall.Personalization.filter_type} />
            </Suspense>
          ) : null} */}

        <ThemeRoute key={wall.product} {...this.props} brandingNetworks={brandNet} containerSize={this.state.containerSize} parentID={this.state.parentID} />

        {postData && postData.completeDataObject ? (
            <Suspense fallback={<></>}>
              <TikTok_Img_update completeDataObject={postData.completeDataObject} postData={postData.postData} />
            </Suspense>
          ) : null}

        {isDisplay ? <UpgradeScreenPopup see={see} {...this.props} /> : null}

        {isDisplay && (!isEditor || window.location.href.includes("studio=1")) && announcements && Object.keys(announcements).length > 0 ? <Announcements newApi={this.props} /> : null }

        </div>

        {headerPositionBottom ? (
          <Suspense fallback={<></>}>
            <Header wall={wall}  isDisplay={isDisplay}  Banner={Banner} BannerImage={BannerImage} LogoImage={LogoImage} CustomBannerImage={CustomBannerImage} />
          </Suspense>
        ) : null}

        {isDisplay && CustomTicker && Object.keys(CustomTicker).length > 0 && CustomTicker.status === 1 &&
            CustomTicker.ticker_position === "2" ? (
              <Suspense fallback={<></>}>
                <BottomTicker wall={wall} UgcSettings={UgcSettings} />
              </Suspense>
            ) : null }
      </div>
    );
  }
}
const mapStateToProps = (state) => {
  if (state.postHeight.manage_height === true) {
    let postHeight = state.postHeight.data.replace("px", "");
    return {
      postHeight,
      renderId: state.renderId,
      modalPop: state.modalPop,
    };
  } else return { renderId: state.renderId };
};

const mapDispatchToProps = (dispatch) => {
  return {
    managePostHeight: (data) => dispatch(managePostHeight(data)),
    updateDataTheme: (data) => dispatch(updateDataTheme(data)),
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(App);
