import React, { PureComponent, Suspense } from "react";
import { connect } from "react-redux";
import { isMobile } from "react-device-detect";
import { THEME_WIDTH_ADJUSTMENT_MODERN, URLDATA, findFromArray,getPersonalize,isSocialWidget} from "../../utils";
import { AutoScrollThemeEnableLoadData, ShowMoreVisibleThemes } from "../../constants";
import { showPopUP, getDataNextSteps, themePostTracking, gtmAnalyticsAction } from "../../actions/themeActions";
import { suiteTrack } from "../../actions/api";
import ErrorPage from "../Components/ErrorPages";
import Fullscreen from "../Components/Common/Fullscreen";
import { GET_APP_DATA_NEW } from "../../reducers/reducersKeys";

const SeeMore = React.lazy(() => import("./Elements/SeeMore/SeeMore"));
const ThemeBranding = React.lazy(() => import("./Elements/Branding/ThemeBranding"));
const NewsCard = React.lazy(() => import("./NewsCard"));
const ClassicPhoto = React.lazy(() => import("./ClassicPhoto"));
const ClassicCard = React.lazy(() => import("./ClassicCard"));
const ModernCard = React.lazy(() => import("./ModernCard"));
const SquarePhoto = React.lazy(() => import("./SquarePhoto"));
const WidgetTheme = React.lazy(() => import("./WidgetTheme"));
const GalleryPhoto = React.lazy(() => import("./GalleryPhoto"));
const ReelTheme = React.lazy(() => import("./ReelTheme"));
const HighlightTheme = React.lazy(() => import("./HighlightTheme"));
const SinglePost = React.lazy(() => import("./SinglePost"));
const StoryTheme = React.lazy(() => import("./StoryTheme"));
const HorizontalSlider = React.lazy(() => import("./HorizontalSlider"));
const HorizontalColumns = React.lazy(() => import("./HorizontalColumns")); 
const AtmospherePixel = React.lazy(() => import("./Atmosphere/Pixel"));
const AtmosphereFocus = React.lazy(() => import("./Atmosphere/Focus"));
const FallingColumns = React.lazy(() => import("./FallingColumns"));
const SlideShow = React.lazy(() => import("./SlideShow"));
const Waterfalltheme = React.lazy(() => import("./ModernWaterfall/Waterfalltheme"));
const ModernWaterfallExhbit = React.lazy(() => import("./ModernWaterfall/Exhbit"));
const AtmosphereReels = React.lazy(() => import("./Atmosphere/Reels"));

const socialWidget = isSocialWidget();

const ThemeRender = React.memo(
  ({
    renderId,
    wall,
    postData,
    adjustWidth,
    languageSetting,
    clickToShowPopUp,
    onClickToCTA,
    onClickPopUpSlider, 
    cardNumber,
    postSizeInPx,
    parentID,
    themeHeight,
    heightUpdate
  }) => {

    const { themeUpdate, isDisplay, Wall } = wall;
    const intractiveData = localStorage.getItem(`intractivelike${Wall.id}`) || [];
    const hasMoreData = postData.hasMoreData[0]
      ? postData.hasMoreData[0].hasMoreData == false
        ? false
        : postData.hasMoreData[postData.appendData.networkID]
          ? postData.hasMoreData[postData.appendData.networkID].hasMoreData
          : true
      : true;

    const slideDuration = wall.Personalization.slideDelay;
    const themeId = wall.ThemeRule.themeId;
    const numberOfCoumn = wall.ThemeRule.numberOfCoumn;

    if (isDisplay) {
      switch (themeId) {
        case 62:
        case 63:
        case 65:
        case 66:
        case 78:
          return  (
              <Suspense fallback={<></>}>
                 <Waterfalltheme
                    heightUpdate={heightUpdate}
                    key={themeUpdate ? themeUpdate : 0}
                    themeHeight={themeHeight}
                    {...postData}
                    themeUpdate={themeUpdate}
                    hasMoreData={hasMoreData}
                    wall={wall}
                    languageSetting={languageSetting}
                    clickToShowPopUp={clickToShowPopUp}
                  />
            </Suspense>
          ) 
        case 64:
          return  (
            <Suspense fallback={<></>}>
               <ModernWaterfallExhbit
                  heightUpdate={heightUpdate}
                  themeHeight={themeHeight}
                  key={numberOfCoumn ? numberOfCoumn : 5} 
                  {...postData}
                  hasMoreData={hasMoreData}
                  wall={wall}
                  languageSetting={languageSetting}
                  clickToShowPopUp={clickToShowPopUp}
                />
            </Suspense>
          ) 
        case 67:
          return (
            <Suspense fallback={<></>}>
              <AtmospherePixel
                {...postData}
                heightUpdate={heightUpdate}
                themeHeight={themeHeight}
                key={themeUpdate ? themeUpdate : 0}
                hasMoreData={hasMoreData}
                wall={wall}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                cardNumber={cardNumber}
              />
            </Suspense>
          ) 
        case 68:
          return (
            <Suspense fallback={<></>}>
              <AtmosphereFocus
                key={numberOfCoumn ? numberOfCoumn : 7}
                {...postData}
                heightUpdate={heightUpdate}
                themeHeight={themeHeight}
                hasMoreData={hasMoreData}
                wall={wall}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                cardNumber={cardNumber}
              />
            </Suspense>
          ) 
        case 69:
          return  (
            <Suspense fallback={<></>}>
              <AtmosphereReels
                  key={numberOfCoumn ? numberOfCoumn : 5}
                  {...postData} 
                  heightUpdate={heightUpdate}
                  themeHeight={themeHeight}
                  hasMoreData={hasMoreData}
                  wall={wall}
                  languageSetting={languageSetting}
                  clickToShowPopUp={clickToShowPopUp}
                  cardNumber={cardNumber}
                />
            </Suspense>
          ) 
        case 70:
        case 71:
        case 72:
        case 73:
          return (
            <Suspense fallback={<></>}>
              <FallingColumns
                {...postData}
                postData={postData}
                heightUpdate={heightUpdate}
                themeHeight={themeHeight}
                wall={wall}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                hasMoreData={hasMoreData}
                renderId={renderId}
                parentID={parentID}
                postSizeInPx={postSizeInPx}
              />
            </Suspense>
          );
        case 74:
        case 75:
        case 76:
        case 77:
          return  (
            <Suspense fallback={<></>}>
              <SlideShow
                {...postData}
                postData={postData}
                heightUpdate={heightUpdate}
                themeHeight={themeHeight}
                key={slideDuration ? slideDuration : 5}
                hasMoreData={hasMoreData}
                wall={wall}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
              />
            </Suspense>
          ) 
        default:
      }
    } else {
      switch (themeId) {
        case 3:
          return adjustWidth ? (
            <Suspense fallback={<></>}>
              <ClassicPhoto
                {...postData}
                hasMoreData={hasMoreData}
                wall={wall}
                adjustWidth={adjustWidth}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                onClickToCTA={onClickToCTA}
              />
            </Suspense>
          ) : null;
        case 4:
          return adjustWidth ? (
            <Suspense fallback={<></>}>
              <SquarePhoto
                {...postData}
                hasMoreData={hasMoreData}
                wall={wall}
                adjustWidth={adjustWidth}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                cardNumber={cardNumber}
                onClickToCTA={onClickToCTA}
                postSizeInPx={postSizeInPx}
              />{" "}
            </Suspense>
          ) : null;
        case 5:
          return adjustWidth ? (
            <Suspense fallback={<></>}>
              <ClassicCard
                {...postData}
                hasMoreData={hasMoreData}
                wall={wall}
                adjustWidth={adjustWidth}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                onClickToCTA={onClickToCTA}
              />
            </Suspense>
          ) : null;
        case 6:
          return (
            <Suspense fallback={<></>}>
              <ReelTheme
                {...postData}
                wall={wall}
                adjustWidth={adjustWidth}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                onClickPopUpSlider={onClickPopUpSlider}
                hasMoreData={hasMoreData}
                renderId={renderId}
                onClickToCTA={onClickToCTA}
                parentID={parentID}
              />
            </Suspense>
          );
        case 16:
          return (
            <Suspense fallback={<></>}>
              <HorizontalSlider
                {...postData}
                hasMoreData={hasMoreData}
                wall={wall}
                adjustWidth={adjustWidth}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                onClickToCTA={onClickToCTA}
                postSizeInPx={postSizeInPx}
                onClickPopUpSlider={onClickPopUpSlider}
                parentID={parentID}
              />
            </Suspense>
          );
        case 19:
          return adjustWidth ? (
            <Suspense fallback={<></>}>
              <NewsCard
                {...postData}
                hasMoreData={hasMoreData}
                wall={wall}
                adjustWidth={adjustWidth}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                onClickToCTA={onClickToCTA}
              />{" "}
            </Suspense>
          ) : null;
        case 20:
          return adjustWidth ? (
            <Suspense fallback={<></>}>
              <ModernCard
                {...postData}
                hasMoreData={hasMoreData}
                wall={wall}
                adjustWidth={adjustWidth}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                onClickToCTA={onClickToCTA}
              />
            </Suspense>
          ) : null;
        case 47:
          return (
            <Suspense fallback={<></>}>
              <HorizontalColumns
                {...postData}
                wall={wall}
                adjustWidth={adjustWidth}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                onClickPopUpSlider={onClickPopUpSlider}
                hasMoreData={hasMoreData}
                renderId={renderId}
                onClickToCTA={onClickToCTA}
                parentID={parentID}
              />
            </Suspense>
          );
        case 49:
          return (
            <WidgetTheme
              {...postData}
              hasMoreData={hasMoreData}
              wall={wall}
              adjustWidth={adjustWidth}
              languageSetting={languageSetting}
              clickToShowPopUp={clickToShowPopUp}
              onClickToCTA={onClickToCTA}
            />
          );
        case 50:
          return (
            <Suspense fallback={<></>}>
              <GalleryPhoto
                {...postData}
                hasMoreData={hasMoreData}
                wall={wall}
                adjustWidth={adjustWidth}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                cardNumber={cardNumber}
                renderId={renderId}
                onClickToCTA={onClickToCTA}
                postSizeInPx={postSizeInPx}
              />
            </Suspense>
          );
        case 52:
          return (
            <Suspense fallback={<></>}>
              <SinglePost
                {...postData}
                hasMoreData={hasMoreData}
                wall={wall}
                adjustWidth={adjustWidth}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                onClickToCTA={onClickToCTA}
              />
            </Suspense>
          );
        case 55:
          return (
            <Suspense fallback={<></>}>
              <HighlightTheme
                {...postData}
                hasMoreData={hasMoreData}
                wall={wall}
                adjustWidth={adjustWidth}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                onClickToCTA={onClickToCTA}
                onClickPopUpSlider={onClickPopUpSlider}
                parentID={parentID}
              />
            </Suspense>
          );
        case 60:
          return (
            <Suspense fallback={<></>}>
              <StoryTheme
                {...postData}
                wall={wall}
                hasMoreData={hasMoreData}
                adjustWidth={adjustWidth}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                onClickToCTA={onClickToCTA}
                onClickPopUpSlider={onClickPopUpSlider}
                parentID={parentID}
              />
            </Suspense>
          );
        case 62:
        case 63:
        case 64:
        case 65:
        case 66:
        case 78:
        case 74:
        case 75:
        case 76:
        case 77:
          return (
            <Suspense fallback={<></>}>
                <Waterfalltheme
                    {...postData}
                    key={themeUpdate ? themeUpdate : 0}
                    heightUpdate={heightUpdate}
                    themeHeight={themeHeight}
                    hasMoreData={hasMoreData} 
                    wall={wall}
                    languageSetting={languageSetting}
                    clickToShowPopUp={clickToShowPopUp}
                    intractiveData={intractiveData}
                  />
            </Suspense>
          );
        case 67:
        case 69:
          return (
            <Suspense fallback={<></>}>
              <AtmospherePixel
                {...postData}
                key={themeUpdate ? themeUpdate : 0}
                hasMoreData={hasMoreData}
                wall={wall}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                cardNumber={cardNumber}
                intractiveData={intractiveData}
              />
            </Suspense>
          )
        case 68:
          return (
            <Suspense fallback={<></>}>
              <AtmosphereFocus
                key={themeUpdate ? themeUpdate : 0}
                {...postData}
                hasMoreData={hasMoreData}
                wall={wall}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                cardNumber={cardNumber}
                intractiveData={intractiveData}
              />
            </Suspense>
          )
        case 70:
        case 71:
        case 72:
        case 73:
          return (
            <Suspense fallback={<></>}>
              <FallingColumns
                {...postData}
                postData={postData}
                heightUpdate={heightUpdate}
                key={themeUpdate ? themeUpdate : 0}
                wall={wall}
                themeHeight={themeHeight}
                languageSetting={languageSetting}
                clickToShowPopUp={clickToShowPopUp}
                hasMoreData={hasMoreData}
                renderId={renderId}
                parentID={parentID}
                postSizeInPx={postSizeInPx}
                intractiveData={intractiveData}
              />
            </Suspense>
          );
        case 74:
        case 75:
        case 76:
        case 77:
          return  (
            <Suspense fallback={<></>}>  
              <ErrorPage
                error={{
                  themeError: {
                    error_code: 204,
                  },
                  errorWithMessage: {
                    message: "Silde Show theme doesn`t applicabled on widget",
                    type: "SildeShow",
                  },
                }}
              />
            </Suspense>
          ) ;
        default:
      }
    }
  }
);

class ThemeRoute extends PureComponent {
  state = {
    windowWidth: window.innerWidth,
    windowHeight: window.innerHeight,
    adjustWidth: 0,
    cardNumber: 0,
    postSizeInPx: 0,
    isLoaded: false,
    topBanner: 0,
    topEditor: 0,
    ticker: 0,
    themeHeight: 0,
  };
  componentWillMount() {
    const { renderId } = this.props.renderId;
    this.setState({
      windowWidth: renderId
        ? document.getElementById(renderId).clientWidth
        : window.innerWidth,
    });
  }

  componentDidMount() {
    const { renderId } = this.props.renderId;
    const { wall } = this.props;
   // this.onResponsiveTheme();
    // window.addEventListener &&
    //   window.addEventListener("resize", () => {
    //     this.setState(
    //       {
    //         windowWidth: renderId
    //           ? document.getElementById(renderId).clientWidth
    //           : window.innerWidth,
    //         windowHeight: window.innerHeight,

    //       },
    //       () => this.onResponsiveTheme()
    //     );
    //   });
    
 
    if(wall.isDisplay){
       if(wall.Personalization.postFeatured == 1){
            if(!getPersonalize()) this.loadPopUpWithTimer();
            this.heightUpdate()
        }
        document.querySelector("body").style.overflow = "hidden";
     }

      if(!wall.isDisplay){  
        if (window.editor) {
          let wallEditor = document.getElementsByClassName("wall-editor");
          wallEditor.length > 0 &&
            wallEditor[0].addEventListener("scroll", this.autoLoadEditorScrollData);
        } else {
          window.addEventListener &&
            window.addEventListener("scroll", this.autoLoadScrollData);
        }
     } 
  }

  autoLoadEditorScrollData = (event) => { 
    const { wall, postData, loaderData } = this.props;
    const postCount = wall.ThemeRule.numberOfPosts;
    let wallEditor = document.getElementsByClassName("wall-editor");
    if (
      wall.Personalization.autoScrollStatus == 1 &&
      AutoScrollThemeEnableLoadData.includes(wall.ThemeRule.themeId) &&
      wallEditor.length > 0
    ) {
      let fixedHeightScrollBar = wallEditor[0];
      let documentHeight = document.body.scrollHeight;
      if (
        documentHeight + fixedHeightScrollBar.scrollTop >=
        fixedHeightScrollBar.scrollHeight - 10
      ) {
        if (!(loaderData && loaderData.isShowMoreLoading)) {
          const timeStamp = Math.floor(Date.now() / 1000);
          this.props.getDataNextSteps(
            wall.Wall.id,
            timeStamp,
            postCount,
            postData.appendData.networkID,
            postData.appendData.after,
            postData.appendData.heightEvent
          );
        }
      }
    }
  };

  onResponsiveTheme = (openAside) => {
      const { wall } = this.props;
      let { windowWidth } = this.state;
      windowWidth = openAside === 2 ? windowWidth + 165 : windowWidth;
      let { adjustWidth, cardNumber } = THEME_WIDTH_ADJUSTMENT_MODERN(
        windowWidth,
        wall
      );

      let postWidth = (100 / windowWidth) * adjustWidth;
      adjustWidth = postWidth.toFixed(2);
      this.setState({
        adjustWidth,
        cardNumber,
        postSizeInPx: THEME_WIDTH_ADJUSTMENT_MODERN(windowWidth, wall),
      });
  };

  autoLoadScrollData = (event) => { 
    const { wall, postData, loaderData } = this.props;
    const postCount = wall.ThemeRule.numberOfPosts;
    const body = document.body;
    const html = document.documentElement;
    if (
      wall.Personalization.autoScrollStatus === 1 &&
      AutoScrollThemeEnableLoadData.includes(wall.ThemeRule.themeId)
    ) {
      const height = Math.max(
        body.scrollHeight,
        body.offsetHeight,
        html.clientHeight,
        html.scrollHeight,
        html.offsetHeight
      );
      if (
        window.innerHeight + window.pageYOffset >=
        parseInt(height) - parseInt(50)
      ) {
        if (!(loaderData && loaderData.isShowMoreLoading)) {
          const timeStamp = Math.floor(Date.now() / 1000);
          this.props.getDataNextSteps(
            wall.Wall.id,
            timeStamp,
            postCount,
            postData.appendData.networkID,
            postData.appendData.after,
            postData.appendData.heightEvent
          );
        }
      }
    }
  };
   
  getPostWidth = (rootWidth, postWidth) => {    
    let cardNumber = rootWidth / postWidth;
    let spacing = rootWidth % postWidth;
    let adjustWidth = null;
    let adjustSpacing = null;
    let rowCard = Math.trunc(cardNumber);
    adjustSpacing = spacing / rowCard;

    if (cardNumber >= 1) adjustWidth = postWidth + adjustSpacing;
    else adjustWidth = postWidth - adjustSpacing;

    return adjustWidth;
  };


  onClickToShowPopup = (currentIndex, item) => (event) => this.onLoadPopUp(currentIndex, item);
  onClickPopUpSlider = (currentIndex, item) => this.onLoadPopUp(currentIndex, item);
  onLoadPopUp = (currentIndex, item) => {
    const { wall, languageSetting, postData } = this.props;
    const { Personalization, ThemeRule } = wall;
    const mobileDevice = isMobile && window.innerWidth < 920;
    if (mobileDevice && wall.Personalization.mobilePopup === 0) {
      if (item.link == "undefined") return false;
      window.open(item.link, "_blank");
    } else {
      let updatePostData = postData.postData.map(
        (pItem) => postData.completeDataObject[pItem]
      );
      let filteredPostData = updatePostData.filter(
        (filterData) => !String(filterData.id).includes("free_add_")
      );
      let nIndex = findFromArray(filteredPostData, item.id, currentIndex);

      if (Personalization.postFeatured === 2) {
        if (item.link == "undefined" || item.link == "") return false;
        window.open(item.link, "_blank");
      } else if (Personalization.postFeatured === 3)
        window.open(Personalization.customUrl, "_blank");
      else {
        if (Personalization.postFeatured === 1 || socialWidget || wall.product){
          this.props.showPopUP({
            type: "post",
            card: item,
            idArray: filteredPostData,
            index: nIndex,
            viewOnText: languageSetting.viewOnText,
            shareText: languageSetting.shareText,
            personalization: Personalization,
            imgData: URLDATA,
            ThemeRule: ThemeRule,
            wall: wall,
          });
        }
      }
    }

    themePostTracking(
      suiteTrack
        ? {
          action: 2,
          wall: wall.Wall.id,
          posts: [
            {
              feed: item.feedId,
              post: item.referenceId ? item.referenceId : item.id,
            },
          ],
          owner: wall.Wall.owner,
        }
        : {
          action: 2,
          wall: wall.Wall.id,
          feed: item.feedId,
          post: item.referenceId ? item.referenceId : item.id,
          owner: wall.Wall.owner,
        }
    );
    gtmAnalyticsAction("Tagbox", "popupclick_", "popup", item.link);
  };

  loadPopUpWithTimer = () => {
    const { postData } = this.props;
    let updatePostData = postData.postData.map(
      (pItem) => postData.completeDataObject[pItem]
    );
    setTimeout(() => this.onLoadPopUp(1, updatePostData[0]), 5000);
  };

  onClickToCTA = (item) => (event) => {
    const { wall } = this.props;
    themePostTracking({
      action: 2,
      wall: wall.Wall.id,
      type: 1,
      feed: item.feedId,
      post: item.referenceId ? item.referenceId : item.id,
    });
    gtmAnalyticsAction(
      "Tagbox",
      "ctaClick",
      "cta",
      item.cta && item.cta.url ? item.cta.url : ""
    );
  };
 
  heightUpdate = () => {

    const { isDisplay, isEditor } = this.props.wall

    if(isDisplay || getPersonalize()){
      var themeBanner = 0;
      var editorHeader = 0;
      var ticker = 0;
      var onsite_upload = 0;

      setTimeout(() => {
     
        const tb_wall_header__ = document.querySelector(".tb_wall_header__");
        const onsite_upload_ = document.querySelector(".tb_onsite_upload_btn_wrap");
        const kt_header = document.getElementById("kt_header");
        const wtickerTop = document.getElementById("wtickerTop");
        const wticker = document.getElementById(wtickerTop ? "wtickerTop" : "wtickerBottom")

          if (tb_wall_header__)  themeBanner = tb_wall_header__.clientHeight;
          if (kt_header && isEditor)  editorHeader = kt_header.clientHeight
          if (wticker)  ticker = wticker.clientHeight; 
          if (onsite_upload_)  onsite_upload = onsite_upload_.clientHeight; 

        const heightFit = themeBanner + editorHeader + ticker + onsite_upload;
        this.setState({ themeHeight: heightFit });

      }, isEditor ? 1000 : 500);
    }
 };

 componentDidUpdate(prevProps) {
    if (getPersonalize() &&  this.props.wall?.themeRender !== prevProps.wall?.themeRender && this.props.appData.postData.postData.length > 32 ) {
      this.props.appData.postData["postData"] = this.props.appData.postData.postData.slice(0, 32);
      this.props.updateAppData(this.props.appData.postData)
    }
}
 
  componentWillUnmount() {
    window.addEventListener && window.addEventListener("resize", this.onResponsiveTheme);
  }

  render() {
    
    const { wall, languageSetting, postData, containerSize, themewallData, parentID, brandingNetworks } = this.props;
    const { ThemeRule,isDisplay,isEditor, themeRender } = wall;
    window.onResponsiveTheme = this.onResponsiveTheme;

    return (
      <div className="tb_theme_container">

        { isDisplay && !isEditor ? <Fullscreen/> : null }

        <ThemeRender
          {...this.props}
          {...this.state}
          onResponsiveTheme={this.onResponsiveTheme}
          heightUpdate={this.heightUpdate} themewallData={themewallData} clickToShowPopUp={this.onClickToShowPopup}
          onClickToCTA={this.onClickToCTA} onClickPopUpSlider={this.onClickPopUpSlider} parentID={parentID} />

        {!isDisplay && ![70, 71, 73, 74, 75, 76, 77].includes(ThemeRule.themeId) && wall.Personalization.loadMoreStatus ? (
          <Suspense fallback={<></>}>
            <SeeMore
              languageSetting={languageSetting}
              wall={wall}
              appendData={postData.appendData}
              postData={postData}
              key={themeRender}
            />
          </Suspense>
        ) : null}

        {!isDisplay && postData.isFreeAdsStatus ? (
          <Suspense fallback={<></>}>
            <ThemeBranding
              containerSize={containerSize}
              languageSetting={languageSetting}
              brandingNetworks={brandingNetworks}
              wallId={wall.Wall.id}
            />{" "}
          </Suspense>
        ) : null}

      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    loaderData: state.loaderData,
    renderId: state.renderId,
    modalPop: state.modalPop,
    appData: state.appData
  }
}

const mapDispatchToProps = dispatch => {
  return {
    showPopUP: (data) => dispatch(showPopUP(data)),
    getDataNextSteps: (wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow) => dispatch(getDataNextSteps(wallID, timeStamp, postCount, networkId, after, heightEvent, updateInStateNow)),
    updateAppData: (data) => dispatch({ type: GET_APP_DATA_NEW, payload: data })
  }
}

export default connect(mapStateToProps, mapDispatchToProps)(ThemeRoute);
