import React, { PureComponent } from "react";
import { isMobile } from "react-device-detect";
import { iniFrame, isEnableFullScreenButton } from "../../../utils";
export default class Fullscreen extends PureComponent {

        constructor(){
            super()
            this.state = {
                controlFullscreen : true
            }
        }

        enableFullScreen = event => {
        let elem = document.documentElement;
        if (elem.requestFullscreen) elem.requestFullscreen();
        else if (elem.mozRequestFullScreen) elem.mozRequestFullScreen();
        else if (elem.webkitRequestFullscreen)  elem.webkitRequestFullscreen();
        else if (elem.msRequestFullscreen) elem.msRequestFullscreen();
        this.setState({ controlFullscreen : false})
        event.preventDefault();
    }

    componentDidMount() {
       this.fullScreen= setTimeout(() => this.setState({ controlFullscreen : false}) , 5000)
    }

    componentWillUnmount(){
        if(this.fullScreen) clearTimeout(this.fullScreen)
    }

  render() {

    return (
         (this.state.controlFullscreen &&  window.innerWidth < 1920 && !isMobile) && !window.location.href.includes('studio=1') && !iniFrame() && isEnableFullScreenButton() && <a id="enableFullScreen" className="commanBtn btn-sm triggerFullScreen" onClick={this.enableFullScreen}>
        <i className="fa fa-arrows-alt" aria-hidden="true"></i>&nbsp; Enable Full Screen</a>
    )
  }
}